/*** upload form ***/

#upload {
  padding: rem-calc(40px 0);
  h1{
    font: 18px/1em $bold-font;
    color: #000;
    text-align: center;
  }
  .file-row {
    text-align: center;
    padding: rem-calc(20px 0);
  }
  .submission-type {
    display: inline-block;
    width: rem-calc(75px);
    height: rem-calc(75px);
    margin: rem-calc(0 20px);
    border: 3px solid #000;
    font: 12px/75px $bold-font;
    color: #000;
    vertical-align: top;
    cursor: pointer;
    
    &.upload-button {
      position: relative;
      overflow: hidden;
      input {
        display: block !important;
        width: rem-calc(75px) !important;;
        height: rem-calc(75px) !important;;
        overflow: hidden !important;
        position: absolute;
        top: -3px;
        left: -3px;
        outline: none;
        margin: 0;
        opacity: 0 !important;
        cursor: pointer;
      }

    }
    &:hover, &.active {
      background: #000;
      color: #fff;
    }
  }
  .col {
    width: 50%;
    float: left;
    padding: 0 rem-calc(10px);
  }
  .login {
    padding: 0 0.625rem;   
    a {
      color: #000;
      font-family: $bold-font;
      font-size: rem-calc(14px);
    } 
  }
  .input-row {
    padding: rem-calc(0 0 10px);
    &:after{
      content:'';
      display: block;
      clear: both; 
    }
    input[type='text']{
      border:none;
      background: #000;
      color:#fff;
      box-shadow: none;
      font-family: $bold-font;
      font-size: rem-calc(14px);
      outline: none;
        &:focus {
          color:#000;
          background: #fff;
        }
        &:focus::-webkit-input-placeholder {
          color: #000;
        }
        &:focus::-moz-placeholder {
          color: #000;
        }
        &:focus:-ms-input-placeholder  {
          color: #000;
        }
    }
    textarea{
      border:none;
      background: #000;
      color:#fff;
      box-shadow: none;
      font-size: rem-calc(12px);
      resize:none;
      outline: none;
      height: rem-calc(100px);
        &:focus {
          color:#000;
          background: #fff;
        }
        &:focus::-webkit-input-placeholder {
          color: #000;
        }
        &:focus::-moz-placeholder {
          color: #000;
        }
        &:focus:-ms-input-placeholder  {
          color: #000;
        }

    }
     ::-webkit-input-placeholder {
       color: #fff;
      font-family: $bold-font;
      font-size: rem-calc(14px);
    }
    ::-moz-placeholder {  
       color: #fff;
      font-family: $bold-font;
       font-size: rem-calc(14px);
    }
    :-ms-input-placeholder {  
       color: #fff;
      font-family: $bold-font;
      font-size: rem-calc(14px);
    }
    select {
      border:none;
      background-color: #000;
      color:#fff;
      font-family: $bold-font;
      font-size: rem-calc(14px);
      outline: none;
      background-image: url('../images/downarrow.png');
      background-size: 14px auto;
    }
    .upload-thumbnail {
      .upload-button {
        position: relative;
        overflow: hidden;
        width: 2.4375rem;
        height: 2.4375rem;
        color: #fff;
        background: #000;
        text-align: center;
        line-height: 2.4375rem;
        font-family: $bold-font;
        font-size: 1.5rem;
        cursor: pointer;
        display: inline-block;
        vertical-align: top;
        input {
          display: block !important;
          width: 2.4375rem !important;
          height: 2.4375rem !important;
          overflow: hidden !important;
          position: absolute;
          top: 0px;
          left: 0px;
          outline: none;
          margin: 0;
          opacity: 0 !important;
          cursor: pointer;
        }
      }
      span {
        display: inline-block;
        vertical-align: top;
        line-height: 2.4375rem;
        font-family: $bold-font;
        font-size: rem-calc(14px);
        color: #000;
        padding: 0 0 0 rem-calc(7px);
      }
    }
    span {
      &.instructions {
        font-size: 11px;
        color: #000;
        font-weight: bold;
        letter-spacing: -.5px;
      }
    }

  }
  .submit-row {
    padding: rem-calc(0 10px);
    input[type='submit'], .rt-button {
      border:none;
      background: #000;
      color: #fff;
      outline: none;
      line-height: 2.5rem;
      height: 2.5rem;
      font-size: 1.75rem;
      margin: 0 rem-calc(20px) 0 0;
    }
  }
}

/*** modal ***/

.modal {
  display: none;
  position: absolute;
  &.open {
    display: block;
  }
  .overlay {
    position: fixed;
    z-index: 9998;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0,0,0,0.6);
  }
  .modal-content {
    position: fixed;
    z-index: 9999;
    top: 15%;
    height: 75%;
    width: 60%;
    left:20%;
    background: #fff;
    padding: rem-calc(25px);

    textarea {
      height: calc(100% - 2.75rem);
      margin: 0;
      resize:none;
      border: none !important;
      box-shadow: none;
      overflow-y:scroll;
      font-size: rem-calc(10px);
      line-height: rem-calc(18px);
      color: #000;
    }
    ::-webkit-input-placeholder {
       color: #000;
       font-family: $bold-font;
       text-transform: uppercase;
    }
    ::-moz-placeholder {  
       color: #000;
       font-family: $bold-font;
       text-transform: uppercase;
    }
    :-ms-input-placeholder {  
       color: #000;
       font-family: $bold-font;
       text-transform: uppercase;
    }
   .bottom {
      height: 2.5rem;
      margin: 0.75rem 0 0;
    }
    .rt-button {
      background: #000;
      color: #fff;
      margin: 0 rem-calc(7px 0);
      vertical-align: top;
    }

  }
}
.modal#viewed-all {
  .modal-content {
      height: auto;
      width: 50%;
      left: 25%;
      background: #fff;
      padding: 3rem 1.5625rem;
      text-align: center;
      p {
        color: #000;
        font-family: $bold-font;
        font-size: rem-calc(13px);
        letter-spacing: 0.08em;
      }
      .button-row {
        margin: 0 0 rem-calc(15px); 
      }
      .button-row:last-of-type {
        margin: 0;
      }
  }
}
.modal#submit-later {
  .modal-content {
    text-align: center;
    color: #000;
    height: auto;
    padding: 1.5625rem 10%;
    h3 {
      font-size: rem-calc(48px);
      line-height: 1em;
      letter-spacing: 0.05em;
      margin: 0 0 rem-calc(5px);
    }
    p {
      font-size: rem-calc(10px);
      text-transform: uppercase;
      margin: rem-calc(5px 0 12px);
    }
    form {
      input[type='text']{
        background: $primary-color;
        color: #000;
        border: none;
        box-shadow: none;
        font-size: rem-calc(12px);
        height: rem-calc(32px);
        line-height: rem-calc(32px);
        letter-spacing: 0.05em;
        margin: 0 0 1.15rem;
      }
      input[type='submit'], .rt-button {
        border:none;
        margin: 0 rem-calc(10px);
        outline: none;
      }
    }
  }
}
.modal#comment-form {
  .modal-content {
    height: auto;
    top: 10%;
    padding: 1rem;
    textarea {
      height: rem-calc(180px);
      padding: 0 0.25rem;
    }
    .submit-row {
      text-align: center;
    }
  }
}
