.modal#login-form, .modal#registered-email-login-form {
  .modal-content {
    background: #000;
    color: #fff;
    top: 10%;
    padding: 1.5rem 1rem;
    height: auto;
    h1 {
      color: #fff;
        font-family: $skinny-font;
        font-size: rem-calc(50px);
