        line-height: 1.1em;
        padding: 0 0 rem-calc(10px) 0;
    }
    .lost-password-form-container {
      display: none;
      
    } 
  }
}

/*** Single Submission ***/
body.single-submission {
  .container {
    padding: 3.5rem 1.5rem 0;
      .main-content {
        padding: 1rem 0;
        text-align: left;
        &:after{
          clear:both;
          content: '';
          display: block;
        }
        .left {
          float: left;
          width: 52%;
          .media-holder {
            .videoWrapper {
              position: relative;
              padding-bottom: 56.25%; /* 16:9 */
              padding-top: 25px;
              height: 0;
              iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
              }
            }
            .audioWrapper iframe {
              width: 100% !important;
            }
            p.text {
              font-family: $bold-font;
              font-size: rem-calc(12px);
            }
          }
        }
        .right {
          float: right;
          width: 47%;
          padding: 0 0 0 1.5rem;
          h1.submission-title {
            font-size: 3rem;
            line-height: 1em;
            margin: 0;
          }
          .meta {
            border-bottom: 1px solid #000;
            text-transform: uppercase;
            padding: rem-calc(3px) 0 0 0;
            .data-label {
              display: inline-block;
              color: $dark-gray;
              font-family: $bold-font;
              font-size: rem-calc(9px);
              letter-spacing: 0.07em;
              vertical-align: middle;
              line-height: rem-calc(25px);
            }
            .data {
              display: inline-block;
              font-family: $skinny-font;
              font-size: rem-calc(18px);
              letter-spacing: 0.04em;
              vertical-align: middle;
              line-height: rem-calc(25px);
            }
            &.user {
              padding: 0;
              .data-label, .data {
                color: #000;
                font-size: rem-calc(10px);
                font-family: $bold-font;
              }
              .data-label {
                font-family: $body-font-family;
              }
            }
          }
          .description {
            font-size: rem-calc(11px);
            line-height: 1.5em;
            padding: rem-calc(12px 0 0);
            .description-label {
                font-size: rem-calc(7px);
                font-family: $bold-font;
                letter-spacing: 0.07em;
                text-transform: uppercase;
                color: $dark-gray;
                padding: 0 0 rem-calc(5px);
            }
            p {
              margin-bottom: 0.75rem;
            }
          }
          .button-row .rt-button {
            outline: none;
            font-size: 1.2rem;
            line-height: 1.75rem;
            margin-right: rem-calc(5px);
          }
        }
        #comments {
          border-top: 1px solid #000;
          margin-top: rem-calc(20px);
          h3 {
            color: $primary-color;
            font-size: 1.5rem;
            margin: rem-calc(12px 0 8px);
          }
          .row {
            margin: 0 0 rem-calc(10px);
          }
          .comment-meta {
                font-size: rem-calc(7px);
                font-family: $bold-font;
                letter-spacing: 0.07em;
                text-transform: uppercase;
                color: $dark-gray;
          }
          .comment-content {
            font-size: rem-calc(11px);
            line-height: 1.5em;
            p {
              margin:0;
            }
          }
        }
      }
  }
}
/*** Gallery ***/
body.page-template-community-gallery, body.archive {
  .container {
    padding: 0;
    #gallery-top {
      height: rem-calc(55px);
      position: fixed;
      z-index: 99;
      top: rem-calc(65px);
      width: 100%;
      .gallery-title {
        background: #000;
        float: left;
        width: 50%;
        height: rem-calc(55px);
        padding: 0 1.5rem;
        font-family: $bold-font;
        font-size: rem-calc(20px);
        line-height: rem-calc(55px);

      }
      .gallery-filter {
        background: #fff;
        float: right;
        width: 50%;
        height: rem-calc(55px);
        z-index: 999;
        .dropdown {
          width: 34%;
          float: left;
          &.theme {
          width: 29%;

          }
          &.language {
          width: 37%;

          }
          .toggle-list {
            color: #000;
            font-family: $bold-font;
            font-size: rem-calc(11px);
            height: rem-calc(55px);
            line-height: rem-calc(55px);
            padding: 0.25rem 0 0 1rem;
            border-right: 1px solid $primary-color;
            cursor: pointer;
            overflow: hidden;
            letter-spacing: 0.08em;
            @include transition(all);
            &:hover {
              background-color: $primary-color;
            }
            &:after {
              display: inline-block;
              content: '';
              width: 11px;
              height: 7px;
              background: url("../images/sprites_2x.png") 0 -333px no-repeat;
              background-size: 250px auto;   
              margin-left: 3px;
              position: relative;
              top: -1px;         
            }

          }
          &:last-child .toggle-list {
            border:none;
          }
          .menu {
            display: none;
            background-color: #000;
            padding: 0.75rem 1rem;
            ul {
              list-style: none;
              margin: 0;
              li {
                  display: block;
                a {
                  font-family: $bold-font;
                  font-size: rem-calc(9px);
                  line-height: 1em;
                  padding: rem-calc(5px) 0;
                  text-transform: uppercase;
                  color: #fff;
                  display: block;
                  letter-spacing: 0.06em;
                  &:hover {
                    color: $primary-color;
                  }

                }
              }
            }

          }
        }
      }
    }
    .gallery-list {
      &:after {
        clear:both;
        content: '';
        display: block;
      }
      padding-top: rem-calc(120px);
      .grid {
        width: 25%;
        float: left;
        padding: 0 0 25% 0;
        background-size: cover;
        background-position: center center;
        position: relative;
        a {
          position: absolute;
          width: 100%;
          height: 100%;
          display: block;
          @include transition(all);
         &:hover {
            background-color: rgba(192,203,37,0.5);
          }
          span {
            position: absolute;
            top: 0;
            right: 0;
            color: #fff;
            background: #000;
            text-align: center;
            font-family: $bold-font;
            height: rem-calc(20px);
            width: rem-calc(20px);
            font-size: rem-calc(16px);
            line-height: 1em;
            padding-top: 3px;

          }
        }
      }
    }
    #bottom-key {
      height: rem-calc(32px);
      position: fixed;
      z-index: 99;
      bottom: 0;
      width: 100%;
      background: #000;
      color: $primary-color;
      text-align: center;
      .key {
        width: 20%;
        display: inline-block;
        font-size: rem-calc(12px);
        font-family: $bold-font;
        line-height: rem-calc(32px);
        span {
          color: #fff;
        }
      }
    }
  }
}
/*** Default Page Template ***/
body.page-template-default,
body.page-template-page-green-background {
  .container {
    padding: 3.5rem 1.5rem 3.125rem;
    #page {
      padding:1rem 0;
        header {
        float: left;
        width: 40%;
        padding: 0 2em 0 0;
        h1 {
          color: #fff;
          font-size: rem-calc(70px);
          letter-spacing: 0.04em;

        }
      }
      .entry-content {
        float: right;
        width: 60%;
        padding: rem-calc(4px) 3em 0 0;

        p {
          font-size: rem-calc(12px);
          line-height: 1.5em;
          margin-bottom: 1.25rem;
        }
        .rt-button {
          font-size: rem-calc(30px);
          line-height: rem-calc(38px);
          letter-spacing: 0.05em;
        }
        h4 {
          font-family: $bold-font;
          font-size: rem-calc(12px);
          line-height: 1em;
          margin-bottom: 0;
          text-transform: uppercase;
        }
      }
    &:before {
      content: '';
      height: 20px;
      display: block;
      position: fixed;
      z-index: 9;
      bottom: 0;
      left:0;
      right: 0;
      background: #000 url('../images/diagonal-chartreuse.png') repeat-x;
      background-size: auto 100%;
    }
    &:after {
      clear: both;
      content: '';
      display: block;
    }

    }
  }
}
body.page-template-page-green-background {
  background: $primary-color;
  color: #000;
  .container #page {
    header h1 {
      color: #000;
    }
    .entry-content {
      a {
        color: #000;
        font-family: $bold-font;
      }
    }
    &:before {
      background: $primary-color url('../images/diagonal-white.png') repeat-x;
      background-size: auto 100%;
    }  
  }
}
.page-template-upload:after,
.single-submission:after {
       content: '';
      height: 20px;
      display: block;
      position: fixed;
      z-index: 9;
      bottom: 0;
      left:0;
      right: 0;
      background: #fff url('../images/diagonal-chartreuse.png') repeat-x;
      background-size: auto 100%;
}
.page-template-upload:after {
      background: $primary-color url('../images/diagonal-white.png') repeat-x;
      background-size: auto 100%;
}